.right-about {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-top: 30px;
}

.right-about .description {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.right-about .description h3 {
  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  text-align: left;
}

.right-about .brief {
}

.right-about .brief p {
  font-family: Inter;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  margin: 6px 0;
}

.right-about .comments .big-title {
  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  text-align: left;
  margin-bottom: 16px;
}

.right-about .comments .all-comments .info .commenter-name {
  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  text-align: left;
}

.right-about .comments .all-comments .info .commenter-name small {
  font-family: Inter;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  text-align: left;
}

.right-about .comments .all-comments .info .comment-text {
  font-family: Inter;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-align: left;
  width: 298px;
  height: 40px;
}

.right-about .comments .all-comments .comment .actions {
  margin-top: 6px;
}

.right-about .comments .all-comments .comment .actions span {
  font-family: Inter;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  text-align: left;
  cursor: pointer;
}

.right-about .comments .type {
  margin: 42px 0 30px;
}

.right-about .comments .type .icon {
}

.right-about .comments .type .input {
  width: 100%;
  position: relative;
}

.right-about .comments .type .input input {
  outline: none;
  background-color: transparent;
  border: 1px solid;
  padding: 5px 17px;
  border-radius: 0.25rem;
  width: 100%;
  background: rgba(255, 255, 255, 0);
}
.right-about .comments .type .input .send {
  width: 69px;
  height: 76%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: var(--main-color);
  color: white;
  text-transform: capitalize;
  font-size: 15px;
  padding: 4px 6px;
  border-radius: 7px;
  position: absolute;
  right: 3px;
  top: 3px;
}
