.learning {
}

.learning .comments {
}

.learning .comments .type {
  margin: 42px 0 30px;
}

.learning .comments .type .icon {
}

.learning .comments .type .input {
  width: 100%;
  position: relative;
}

.learning .comments .type .input input {
  outline: none;
  background-color: transparent;
  border: 1px solid;
  padding: 5px 17px;
  border-radius: 0.25rem;
  width: 100%;
  background: rgba(255, 255, 255, 0);
}

.learning .comments .latest-updates .text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.75rem;
  margin-bottom: 12px;
}

.learning .comments .latest-updates .text strong {
  font-family: Inter;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25rem;
  /* 142.857% */
}
.learning .comments .latest-updates .all-comments {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.learning .comments .latest-updates .comment {
  padding: 0.75rem;
  border: 1px solid #f3f4f6;
  background: #fff;
  box-shadow: 0px 4px 6px -2px rgba(75, 85, 99, 0.1);
}

.learning .comments .latest-updates .comment > .avatar {
}

.learning .comments .latest-updates .comment .info {
}

.learning .comments .latest-updates .comment .info .title {
}

.learning .comments .latest-updates .comment .title h4 {
  color: #374151;
  font-family: Inter;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25rem;
  /* 142.857% */
}

.learning .comments .latest-updates .comment .info > span {
  font-family: Inter;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25rem;
  /* 142.857% */
}

.learning .comments .latest-updates .comment .info span a {
}

.learning .comments .latest-updates .comment .info .commenter {
  display: flex;
  padding-top: 0.75rem;
  align-items: center;
  gap: 0.5rem;
  align-self: stretch;
}

.learning .comments .latest-updates .comment .info .commenter .avatar {
  width: 1.25rem;
  height: 1.25rem;
}

.learning .comments .latest-updates .comment .info .commenter .name {
  font-family: Inter;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1rem;
  /* 133.333% */
}

.learning .comments .latest-updates .comment .info .commenter span {
  font-family: Inter;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1rem;
  /* 133.333% */
}

.learning .comments .send {
  width: 69px;
  height: 76%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: var(--main-color);
  color: white;
  text-transform: capitalize;
  font-size: 15px;
  padding: 4px 6px;
  border-radius: 7px;
  position: absolute;
  right: 3px;
  top: 3px;
}
