.home {
}

/********************Welcome***********************/
.home .welcome {
  width: 100%;
  padding: 48px 0 0px 0;
  display: flex;
  flex-direction: column;
  gap: 48px;
}

.home .search-input {
}

.home .search-input input {
  border: 1px solid #ccc;
  padding: 7px 19px 7px 36px;
  width: 346px;
  outline: none;
  border-radius: 7px;
  height: 42px;
}

.home .search-input svg {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 7px;
}

@media (max-width: 1280px) {
  .home .welcome {
    width: 100%;
    padding-right: 16px;
  }
}

/********************Tests***********************/

.tests .all-tests {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 12px;
}

.tests .all-tests .cover {
  height: 97px;
  padding: 16px;
  gap: 12px;
  border-radius: 6px 0px 0px 0px;
}

.tests .all-tests .cover .test {
}

.tests .all-tests .cover .test .image-box {
  width: 35px;
  height: 35px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tests .all-tests .cover .test .image-box img {
  width: 20px;
  height: 21.82px;
}

.tests .all-tests .cover .test .info {
}

.tests .all-tests .cover .test .info .subject-student {
}

.tests .all-tests .cover .test .info .subject-student span {
  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  text-align: left;
}

.tests .all-tests .cover .test .info .student h4 {
  font-family: Inter;
  font-size: 11px;
  font-weight: 400;
  line-height: 15px;
  text-align: left;
}

.tests .all-tests .cover .date {
  width: 149px;
  height: 16px;
}

.tests .all-tests .cover .date .icon {
}

.tests .all-tests .cover .date img {
  width: 16px;
  height: 16px;
  gap: 0px;
  opacity: 0px;
}

.tests .all-tests .cover .date span {
  font-family: Inter;
  font-size: 11px;
  font-weight: 400;
  line-height: 16px;
  text-align: left;
}

