/********************Subjects***********************/

.home .subjects .all-subjects {
  display: flex;
  align-items: flex-start;
  align-content: flex-start;
  gap: 1rem;
  align-self: stretch;
  flex-wrap: wrap;
}

.home .subjects .all-subjects .subject {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  width: 166px;
  height: 166px;
  padding: 8px;
  gap: 14px;
  border-radius: 6px;
}

.home .subjects .all-subjects .subject .image-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 148px;
  height: 98px;
}

