/********************Lessons***********************/
.home .lessons .head .search-input img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.home .lessons .head .search-input input {
  border: 1px solid;
  padding: 11px 19px 7px 36px;
  outline: none;
  width: 266.91px;
  height: 32px;
  border-radius: 5.82px;
}

.home .lessons .course .top-section p {
  color: var(--colors-gray-700, #374151);
  font-family: Inter;
  font-size: 0.8125rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25rem;
  /* 153.846% */
}

.home .lessons .course .lesson > span {
  display: flex;
