/********************Right Side***********************/
.home .right-side {
  display: flex;
  padding: 1.5rem 1rem 2rem 1rem;
  flex-direction: column;
  align-items: flex-start;
  gap: 1.5rem;
  border-left: 1px solid;
  width: 21.75rem !important;
}

.right-side .profile-info {
  display: flex;
  width: 19.75rem;
  padding: 1rem;
  flex-direction: column;
  gap: 1rem;
  border-radius: 0.5rem;
  border: 1px solid #f3f4f6;
  box-shadow: 0px 4px 6px -2px rgba(75, 85, 99, 0.1);
}

.right-side .details {
  display: flex;
  padding: 0rem 0.5rem;
  justify-content: space-between;
  align-items: flex-start;
}

.right-side .details .name-class .name {
  font-family: Inter;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25rem;
  /* 142.857% */
}

.right-side .details .name-class .class {
  font-family: Inter;
  font-size: 0.75rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1rem;
  /* 133.333% */
}

.right-side .teachers .top-section p {
  font-family: Inter;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25rem;
  /* 142.857% */
}

.right-side .teachers .box {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid #f3f4f6;
  box-shadow: 0px 4px 6px -2px rgba(75, 85, 99, 0.1);
}

.right-side .teachers .box .info .subject-teacher {
  font-family: Inter;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25rem;
  /* 142.857% */
}

.right-side .teachers .box .info .subject-teacher small {
  font-family: Inter;
  font-size: 0.6875rem;
  font-style: normal;
  font-weight: 400;
  line-height: 0.9375rem;
}

.right-side .teachers .box .info .subject-teacher small p {
  font-family: Inter;
  font-size: 10px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25rem;
  /* 333.333% */
}

.right-side .teachers .notice {
  overflow: hidden;
  text-overflow: ellipsis;
  font-family: Inter;
  font-size: 0.8125rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25rem;
  /* 153.846% */
}

.right-side .teachers .unit {
  overflow: hidden;
  color: var(--colors-gray-700, #374151);
  text-overflow: ellipsis;
  font-family: Inter;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 500;
  line-height: 1.25rem;
  /* 142.857% */
}

.right-side .calender .date {
  display: flex;
  width: 3.25rem;
  height: 4rem;
  padding: 0.5rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
  border: 1px solid #d1d5db;
}

.right-side .calender .date b {
  font-family: Inter;
  font-size: 0.875rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25rem;
  /* 142.857% */
}

.right-side .details .badges {
  display: flex;
  width: 4.75rem;
  justify-content: center;
  align-items: flex-start;
  align-content: flex-start;
  gap: 0.25rem;
  flex-wrap: wrap;
}

.right-side .details .badges strong {
  line-height: 13px;
  font-size: 11px;
  font-weight: 500;
  margin-left: 2px;
}

.right-side .profile-info .name-class {
  margin-left: 9px;
}

.right-side .right-side-button {
  border-radius: 50%;
  width: 25px;
  height: 25px;
  position: absolute;
  left: -89px;
  margin-top: 15px;
}

@media (max-width: 768px) {
  .right-side {
    grid-column: span 2;
  }
}

@media (max-width: 1280px) {
  .right-side {
    position: fixed;
    right: -375px;
    transition: 0.3s;
    width: 375px !important;
  }

  .right-side.open {
    transition: 0.3s;
    right: 0;
    position: absolute;
    bottom: 0;
    top: 0;
  }
}

/********************************General Styles**********************************************/
.head h3 {
  font-family: Inter;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  text-align: left;
}

.head span {
  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
  text-align: left;
}
